  color: white;
  padding-bottom: 20px;
  z-index: 999; }
  nav #logo-container {
    position: relative;
    background-color: black;
    color: white;
    font-size: 12px;
    padding: 5px 0; }
  nav #logo {
    font-size: 40px;
    font-family: "Bangers", cursive;
    font-weight: 900;
    color: #00ffbb;
    text-shadow: 3px 2px orange;
    text-decoration: none; }
  nav #nav-menu p,
  nav #socials p {
